<template>
	<div>
		<div class="shopcart">
			<div class="content" @click="toggleList">
				<div class="content-left">
					<div class="logo-wrapper">
						<div class="logo" :class="{'highlight': totalCount > 0}">
							<i class="icon-shopping_cart" :class="{'highlight': totalCount > 0}"></i>
						</div>
						<div class="num" v-show="totalCount > 0">{{totalCount}}</div>
					</div>
					<div class="price" :class="{'highlight': totalPrice > 0}">￥{{totalPrice}}</div>
					<div class="desc"></div>
				</div>
				<div class="content-right" @click.stop.prevent="pay">
					<div class="pay" :class="payClass">
						{{payDesc}}
					</div>
				</div>
			</div>
			<div class="ball-container">
				<transition name="drop" v-on:before-enter="beforeEnter" v-on:enter="enter" v-on:after-enter="afterEnter" v-for="ball in balls" :key="ball.key">
					<div  v-show="ball.show" class="ball">
						<div class="inner inner-hock"></div>
					</div>
				</transition>
			</div>
			<transition name="fold">
				<div class="shopcart-list" v-show="listShow">
					<div class="list-header">
						<h1 class="title">购物车</h1>
						<span class="empty" @click="empty">清空</span>
					</div>
					<div class="list-content" ref="listContent">
						<ul>
							<li class="food" v-for="food in selectFoods" :key="food.key">
								<span class="name">{{food.foodName}}</span>
								<div class="price">
									<span>￥{{food.foodPrice * food.count}}</span>
								</div>
								<div class="cartcontrol-wrapper">
									<cartcontrol v-on:cart-add="_drop" :food="food"></cartcontrol>
								</div>
							</li>
						</ul>
					</div>
				</div>
			</transition>
		</div>
		<transition name="fade">
			<div class="list-mask" v-show="listShow" @click="hideList"></div>
		</transition>
	</div>
</template>

<script type="text/ecmascript-6">
	import cartcontrol from '@/components/cartcontrol/cartcontrol'
	import BScroll from 'better-scroll'
	import { Dialog } from 'vant'

	export default {
		props: {
			selectFoods: {
				type: Array,
				default () {
					return []
				}
			},
			deliveryPrice: {
				type: Number,
				default: 0
			},
			minPrice: {
				type: Number,
				default: 0
			},
			appl: {
				type: Object
			}
		},
		data () {
			return {
				balls: [{
					show: false
				}, {
					show: false
				}, {
					show: false
				}, {
					show: false
				}, {
					show: false
				}],
				dropBalls: [],
				fold: true
			}
		},
		computed: {
			totalPrice () {
				let total = 0
				this.selectFoods.forEach((food) => {
					total += food.foodPrice * food.count
				})
				return total
			},
			totalCount () {
				let count = 0
				this.selectFoods.forEach((food) => {
					count += food.count
				})
				return count
			},
			payDesc () {
				if (this.totalPrice === 0) {
					return `请选择菜品`
				} else if (this.totalPrice < this.minPrice) {
					let dif = this.minPrice - this.totalPrice
					return `还差￥${dif}元起送`
				} else {
					return '去结算'
				}
			},
			payClass () {
				if (this.totalPrice < this.minPrice) {
					return 'not-enough'
				} else {
					return 'enough'
				}
			},
			listShow () {
				if (!this.totalCount) {
					// eslint-disable-next-line
					this.fold = true
					return false
				}
				let show = !this.fold
				if (show) {
					this.$nextTick(() => {
						if (!this.scroll) {
							// eslint-disable-next-line
							this.scroll = new BScroll(this.$refs.listContent, {click: true})
						} else {
							// eslint-disable-next-line
							this.scroll.refresh()
						}
					})
				}
				return show
			}
		},
		methods: {
			_drop (target) {
				// 体验优化，异步执行下落动画
				this.$nextTick(() => {
					this.drop(target)
				})
			},
			drop (el) {
				for (let i = 0; i < this.balls.length; i++) {
					let ball = this.balls[i]
					if (!ball.show) {
						ball.show = true
						ball.el = el
						this.dropBalls.push(ball)
						return
					}
				}
			},
			beforeEnter: function (el) {
				let count = this.balls.length
				while (count--) {
					let ball = this.balls[count]
					if (ball.show) {
						let rect = ball.el.getBoundingClientRect()
						let x = rect.left - 32
						let y = -(window.innerHeight - rect.top - 22)
						el.style.display = ''
						el.style.webkitTransform = `translate3d(0,${y}px,0)`
						el.style.transform = `translate3d(0,${y}px,0)`
						let inner = el.getElementsByClassName('inner-hock')[0]
						inner.style.webkitTransform = `translate3d(${x}px, 0, 0)`
						inner.style.transform = `translate3d(${x}px, 0, 0)`
					}
				}
			},
			enter: function (el) {
				/* eslint-disable no-unused-vars */
				let rf = el.offsetHeight
				this.$nextTick(() => {
					el.style.webkitTransform = 'translate3d(0, 0, 0)'
					el.style.transform = 'translate3d(0, 0, 0)'
					let inner = el.getElementsByClassName('inner-hock')[0]
					inner.style.webkitTransform = 'translate3d(0, 0, 0)'
					inner.style.transform = 'translate3d(0, 0, 0)'
				})
			},
			afterEnter: function (el) {
				let ball = this.dropBalls[0]
				if (ball) {
					ball.show = false
					el.style.display = 'none'
				}
			},
			toggleList () {
				if (!this.totalCount) {
					return
				}
				this.fold = !this.fold
			},
			empty () {
				this.selectFoods.forEach((food) => {
					food.count = 0
				})
			},
			hideList () {
				this.fold = true
			},
			pay () {
				if (this.totalPrice <= 0) {
					return
				}
				console.log(JSON.stringify(this.selectFoods))
				console.log(JSON.stringify(this.appl))
				if (this.appl == null) {
					// 提单	
					this.$http({
						method: 'post',
						url: 'https://zhl.lululuye.xyz/wpark_test/order/canteen-order/orderInsert',
						headers: {
							'Content-Type': 'application/json'
						},
						data: JSON.stringify(this.selectFoods)
					}).then((response) => {
						// console.log(response)
						response = response.data
						if (response.resultCode === 200) {
							Dialog.alert({
								message: '提交订单成功！'
							}).then(() => {
								this.$router.go(-1)
							})
						} else {
							Dialog({ message: response.resultMsg })
							console.log(response.resultMsg)
						}
					}).catch((error) => {
						Dialog({ message: '服务器开小差，请联系管理员！' })
						console.log(error)
					})
				} else {
					// 修改
					console.log(this.appl)
					this.appl.requestOrderVos = this.selectFoods
					this.$http({
						method: 'post',
						url: 'https://zhl.lululuye.xyz/wpark_test/order/canteen-order/updateCanteenOrder',
						headers: {
							'Content-Type': 'application/json'
						},
						data: JSON.stringify(this.appl)
					}).then((response) => {
						response = response.data
						if (response.resultCode === 200) {
							this.$router.push({ name: 'order' })
						} else {
							window.alert(response.resultMsg)
							console.log(response.resultMsg)
						}
					}).catch((error) => {
						console.log(error)
					})
				}
			}
		},
		components: {
			cartcontrol,
			[Dialog.Component.name]: Dialog.Component
		}
	}
</script>

<style lang="stylus" rel="stylesheet/stylus">
	@import '../../common/stylus/mixin.styl'
	.shopcart
		position: fixed
		left: 0
		bottom: 0
		z-index: 50
		width: 100%
		height: 48px
		.content
			display: flex
			background: #141d27
			font-size: 0
			.content-left
				flex: 1
				.logo-wrapper
					display: inline-block
					position: relative
					top: -10px
					margin: 0 12px
					padding: 6px
					width: 56px
					height: 56px
					box-sizing: border-box
					text-align: center
					vertical-align: top
					border-radius: 50%
					background: #141d27
					.logo
						width: 100%
						height: 100%
						border-radius: 50%
						background: #2b343c
						&.highlight
							background: rgb(0,160,220)
						.icon-shopping_cart
							line-height: 44px
							font-size: 24px
							color:#80858a
							&.highlight
								color: #fff
					.num
						position: absolute
						top: 0
						right: 0
						width: 24px
						height: 16px
						line-height: 16px
						text-align: center
						border-radius: 16px
						font-size: 9px
						font-weight: 700
						color: #fff
						background: rgb(240,20,20)
						box-shadow: 0 4px 8px 0 rgba(0,0,0,0.4)
				.price
					display: inline-block
					vertical-align: top
					margin-top: 12px
					box-sizing: border-box
					padding-right: 12px
					line-height: 24px
					border-right: 1px solid rgba(255,255,255,0.1)
					font-size: 16px
					font-weight: 700
					color: rgba(255,255,255,0.4)
					&.highlight
						color: #fff
				.desc
					display: inline-block
					vertical-align: top
					line-height: 24px
					margin: 12px 0 0 12px
					color: rgba(255,255,255,0.4)
					font-size: 10px
			.content-right
				flex: 0 0 105px
				width: 105px
				.pay
					height: 48px
					line-height: 48px
					text-align: center
					font-size: 12px
					color: rgba(255,255,255,0.4)
					font-weight: 700
					&.not-enough
						background: #2b333b
					&.enough
						background: #3497DA
						color: #fff
		.ball-container
			.ball
				position: fixed
				left: 32px
				bottom: 22px
				z-index: 200
				transition: all 0.4s cubic-bezier(0.49, -0.29, 0.75, 0.41)
				.inner
					width: 16px
					height: 16px
					border-radius: 50%
					background: rgb(0,160,220)
					transition: all 0.4s linear
		.shopcart-list
			position: absolute
			top: 0
			left: 0
			z-index: -1
			width: 100%
			transition: all 0.5s
			transform: translate3d(0,-100%,0)
			&.fold-enter, &.fold-leave-active
				transform: translate3d(0,0,0)
			.list-header
				height: 40px
				line-height: 40px
				padding: 0 18px
				background: #f3f5f7
				boder-bottom: 1px solid rgba(7,17,27,0.1)
				.title
					float: left
					font-size: 14px
					color: rgb(7,17,27)
				.empty
					float: right
					font-size: 12px
					color: rgb(0,160,220)
			.list-content
				padding: 0 18px
				max-height: 217px
				overflow: hidden
				background: #fff
				.food
					position: relative
					padding: 12px 0
					box-sizing: border-box
					border-1px(rgba(7,17,27,0.1))
					.name
						line-height: 24px
						font-size: 14px
						color: rgb(7,17,27)
					.price
						position: absolute
						right: 90px
						bottom: 12px
						line-height: 24px
						font-size: 14px
						font-weight: 700
						color: rgb(240,20,20)
					.cartcontrol-wrapper
						position: absolute
						right: 0
						bottom: 6px
	.list-mask
		position: fixed
		top: 0
		left: 0
		width: 100%
		height: 100%
		z-index: 40
		backdrop-filter: blur(10px)
		opacity: 1
		background: rgba(7,17,27,0.6)
		transition: all 0.5s
		&.fade-enter, &.fade-leave-active
			opacity: 0
			background: rgba(7,17,27,0)
</style>



