import Vue from 'vue'
import Router from 'vue-router'
import Header from '@/components/header/header'
import Goods from '@/components/goods/Goods'
import Order from '@/components/order/order'

Vue.use(Router)

export default new Router({
  model: 'history',
  linkActiveClass: 'active',
  routes: [
    {
      path: '/header',
      name: 'header',
      component: Header
    },
    {
      path: '/goods',
      name: 'goods',
      component: Goods
    },
    {
      path: '/',
      name: 'order',
      component: Order
    }
  ]
})



