<template>
    <div class="goods">
    <van-nav-bar title="周末打包" left-text="返回" left-arrow @click-left="goBack"/>
        <van-loading v-if="showLoading" type="spinner"/>
        <!-- <v-header></v-header> -->
        <div class="goond-content">
        <div class="foods-wrapper" ref="foodswrapper">
            <ul>
                <li v-for="food in foods" class="food-item border-1px food-list-hook" :key="food.key">
                    <div class="content">
                        <h2 class="name">{{food.foodName}}</h2>
                        <div class="extra">
                            <span class="count">(每份{{ food.eachCount }}个)</span>
                        </div>
                        <div class="price">
                            <span class="now">￥{{food.foodPrice}}</span>
                        </div>
                        <div class="cartcontrol-wrapper">
                            <cartcontrol v-on:cart-add="_drop" :food="food"></cartcontrol>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
        <shopcart ref="shopcart" :select-foods="selectFoods" :appl="appl"></shopcart>
        </div>
    </div>
</template>

<script type="text/ecmascript-6">
    import BScroll from 'better-scroll'
    import shopcart from '@/components/shopcart/shopcart'
    import cartcontrol from '@/components/cartcontrol/cartcontrol'
    import header from '@/components/header/header'
    import { Dialog } from 'vant'
    
    export default {
        data() {
            return {
                goods: [],
                foods: [],
                appl: null,
                scrollY: 0,
                showLoading: true
            }
        },
        created() {
            let updateAppl = this.$route.query.updateAppl
            if (updateAppl == null) {
                this.$http({
                    method: 'post',
                    url: 'https://zhl.lululuye.xyz/wpark_test/canteen/menu/findCanteenMenuThisWeek',
                    data: {
                    }
                }).then((response) => {
                    this.showLoading = false
                    response = response.data
                    console.log(response)
                    if (response.resultCode === 200) {
                        if (response.resultData === null) {
                            Dialog({ message: '未到预定时间！' })
                            return
                        }
                        this.foods = response.resultData.menuDetails
                        this.$nextTick(() => {
                            this._initScroll()
                            // this._calculateHeight()
                        })
                    } else {
                        Dialog({ message: response.resultMsg })
                        console.log(response.resultMsg)
                    }
                }).catch((error) => {
                    Dialog({ message: '服务器开小差，请联系管理员！' })
                    console.log(error)
                })
            } else {
                this.appl = JSON.parse(updateAppl)
                console.log(this.appl)
                this.foods = this.appl.foodOrders
                this.$nextTick(() => {
                    this._initScroll()
                    // this._calculateHeight()
                })
            }
        },
        computed: {
			currentIndex () {
				for (let i = 0; i < this.listHeight.length; i++) {
					let height1 = this.listHeight[i]
					let height2 = this.listHeight[i + 1]
					if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
						return i
					}
				}
			},
			selectFoods () {
                let foods = []
                let updateAppl = this.$route.query.updateAppl
                if (updateAppl != null) {
                    this.foods.forEach((food) => {
                        if (food.count) {
                            // TODO
                            food.userId = '555045850'
                            food.branchId = 7
                            food.foodId = food.foodId
                            foods.push(food)
                        }
                    })
                } else {
                    this.foods.forEach((food) => {
                        if (food.count) {
                            // TODO
                            food.userId = '555045850'
                            food.branchId = 7
                            food.foodId = food.id
                            foods.push(food)
                        }
                    })
                }
                console.log('选择的食物')
                console.log(foods)
				return foods
			}
		},
        methods: {
            goBack () {
                this.$router.go(-1)
            },
            _drop (target) {
				// 体验优化，异步执行下落动画
				this.$nextTick(() => {
					this.$refs.shopcart.drop(target)
				})
			},
            _initScroll () {
                this.foodsScroll = new BScroll(this.$refs.foodswrapper, {
                    click: true
                })
            },
            _calculateHeight () {
				let foodList = this.$refs.foodswrapper.getElementsByClassName('food-list-hook')
				let height = 0
				this.listHeight.push(height)
				for (let i = 0; i < foodList.length; i++) {
					let item = foodList[i]
					height += item.clientHeight
					this.listHeight.push(height)
				}
			}
        },
        components: {
            'v-header': header,
            shopcart,
            cartcontrol
            // [Dialog.name]: Dialog
        }
    }
</script>

<style lang="stylus" rel="stylesheet/stylus">
    @import "../../common/stylus/mixin"
    .goods
        .van-loading
            margin: auto
            position: absolute
            top: 0
            left: 0
            right: 0
            bottom: 0
            width: 30px
            height: 30px
        .header
            text-align :center
        .goond-content
            display: flex
            position: absolute
            width: 95%
            left: 10px
            right: 10px
            top: 64px
            bottom: 46px
            overflow: hidden
            .foods-wrapper
                flex: 1
                .food-item
                    display: flex
                    margin: 18px
                    padding-bottom: 18px
                    border-1px(rgba(7,17,27,0.1))
                    &:last-child
                        border-none()
                        margin-bottom: 0
                    .content
                        flex: 1
                        .name
                            margin: 2px 0 8px 0
                            height: 14px
                            line-height: 14px
                            font-size: 14px
                            color: rgb(7,17,27)
                        .extra
                            margin-top: 8px
                            line-height: 10px
                            font-size: 12px
                            color: rgb(147,153,159)
                            .count
                                margin-right: 12px
                        .price
                            font-weight: 700
                            line-height: 24px
                            margin-top: 5px
                            .now
                                margin-right: 8px
                                font-size: 14px
                                color: rgb(240,20,20)
                        .cartcontrol-wrapper
                                position: absolute
                                right: 0
                                bottom: 18px
</style>



