<template>
  <div class="order">
    <van-nav-bar class="nav-top" title="我的订单" left-text="返回" left-arrow @click-left="goBack"/>
    <van-loading v-if="showLoading" size="30px" type="spinner"/>
    <!-- <div class="v-header">
      <h3 style="text-align :center">订单列表</h3>
    </div> -->
    <div class="order-list">
      <div v-for="appl in this.appls" v-bind:key="appl.id" class="order-card">
        <div class="order-card-head">
          <div class="head-logo">
            <img src="./order_tools.png"/>
            <div class="head-title">{{ appl.applNum }}</div>
          </div>
          <div class="order-status" v-if="appl.status === 0">
            打包中 
          </div>
          <div class="order-status" v-else-if="appl.status === 1">
            已完成
          </div>
          <div class="order-status" v-else-if="appl.status === 2">
            已取消
          </div>
        </div>
        <div class="order-card-body">
          <div v-for="foodOrder in appl.foodOrders" v-bind:key="foodOrder.id" class="card-body-content">
            <div style="font-weight: 330">{{ foodOrder.foodName }}&nbsp;
             <span class="menu-content">(每份{{ foodOrder.foodPrice }}元，每份{{ foodOrder.eachCount }}个)</span>
            </div>
            <div>x{{ foodOrder.count }}</div>
          </div>
          <div class="card-body-total">
            <div style="font-size:12px">总价&nbsp;¥<span style="font-size:25px; font-weight:330">{{ appl.foodSumPrice }}</span></div>
          </div>
        </div>
        <div style="height:1px;background:lightgray;opacity:0.7; margin:0px 16px;"></div>
        <div class="order-card-bottom">
          <div class="order-card-date">{{ appl.creatTime }}</div>
          <div v-if="appl.status === 0">
            <button class="cancel-button" @click.stop.prevent="deleteOrder(appl)">取消订单</button>
          </div>
          <div v-else-if="appl.status === 2"> 
            <button class="delete-button" @click.stop.prevent="deleteOrder(appl)">删除订单</button>
          </div>
        </div>
      </div>
    </div>
    <van-button class="add-button" type="default" @click.stop.prevent="addAction()">
        <van-icon name="add" /> 新增订单
    </van-button>
  </div>
</template>

<script type="text/ecmascript-6">
  import header from '@/components/header/header'
  import { Dialog, Button } from 'vant'

  export default {
    data() {
      return {
        statusMsg: '',
        showLoading: true,
        appls: []
      }
    },
    beforeCreate () {
      document.querySelector('body').setAttribute('style', 'background-color:#F5F5F5')
    },
    beforeDestroy () {
      document.querySelector('body').removeAttribute('style')
    },
    created() {
      // Native Mobile Methods
      window.webkit.messageHandlers.WKMesesgaSignal.postMessage('Test JS')
      window.sayHelloCallBack = this.sayHelloCallBack
      this.$http({
          method: 'post',
          url: 'https://zhl.lululuye.xyz/wpark_test/order/canteen-order/queryOrder',
          data: {
            'userId': '555045850'
          }
      }).then((response) => {
          this.showLoading = false
          response = response.data
          console.log(response)
          if (response.resultCode === 200) {
              this.appls = response.resultData
          } else {
              Dialog({ message: response.resultMsg })
              console.log(response.resultMsg)
          }
      }).catch((error) => {
          this.showLoading = false
          Dialog({ message: '服务器开小差，请联系管理员！' })
          console.log(error)
      })
    },
    methods: {
      sayHelloCallBack() {
        alert('你好')
        return 'you get me'
      },
      goBack () {
        this.$router.go(-1)
      },
      addAction() {
        this.$router.push({ name: 'goods' })
      },
      updateOrder(appl) {
        console.log(appl)
        this.$router.push({ 
          path: '/',
          query: {
            updateAppl: JSON.stringify(appl)
          }
        })
      },
      deleteOrder(appl) {
        console.log(appl)
        let foodId = appl.foodOrders[0].foodId
        console.log(foodId)
        this.$http({
            method: 'post',
            url: 'https://zhl.lululuye.xyz/wpark_test/order/canteen-order/deleteCanteenOrder',
            data: {
              'userNum': 555045850,
              'foodId': foodId,
              'createTime': appl.creatTime,
              'applNum': appl.appNum,
              'status': appl.status
            }
        }).then((response) => {
            response = response.data
            console.log(response)
            if (response.resultCode === 200) {
               location.reload()
            } else {
                Dialog({ message: response.resultMsg })
                console.log(response.resultMsg)
            }
        }).catch((error) => {
            Dialog({ message: '服务器开小差，请联系管理员！' })
            console.log(error)
        })
      }
    },
    components: {
      'v-header': header,
      [Button.name]: Button
    }
  }
</script >

<style scoped>

  .van-loading {
    margin: auto;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 30px;
    height: 30px;
  }

  .text {
    font-size: 14px;
  }
  .order-list {
    margin: 46px 0px 68px 0px
  }
  .order-card {
    margin: 20px 10px;
    color: black;
    background-color:white;
    border-radius: 12px;
  }
  .order-card-head {
    display: flex;
    flex-direction: row;
    justify-content:space-between ;
    padding: 16px;
  }
  .order-card .head-logo {
    display: flex;
    flex-direction: row;
  }
  .order-card .head-title {
     margin-left: 2px;
     font-size: 18px;
     font-weight: bold;
     line-height: 20px;
  }
  .order-card .order-status {
    color: rgb(238, 135, 49); 
    font-size: 14px;
    font-weight: 330;
  }
  .order-card-head img{
    height: 20px;
  }
  .order-card-body {
    margin-top: 8px
  }
  .card-body-content{
    padding-right: 16px;
    padding-left: 16px;
    margin-bottom: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .card-body-content .menu-content {
    color: gray; 
    font-size: 13px;
  }
  .card-body-total{
    margin: 18px 0px;
    padding-right: 16px;
    display: flex;
    justify-content: flex-end;
  }
  
  .order-card-bottom{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .order-card-bottom .order-card-date {
    color: gray; 
    font-size: 13px;
    margin-top: 8px;
    line-height: 40px;
    margin-left: 16px;
  }
  .order-card-bottom button{
    width: 100px;
    height: 35px;
    font-size: 12px;
    border-radius: 30px;
  }
  .order-card-bottom .cancel-button {
    color:white;
    margin: 10px 16px 10px 0px; 
    background-color: rgb(238, 135, 49);
    border: none;
    border-color: lightgray;
  }

  .order-card-bottom .delete-button {
    border: none;
    background-color: lightgray;
    border-width: 0.5px;
    margin: 10px 16px 10px 0px;
  }

  .add-button {
    position: fixed;
		left: 0;
		bottom: 0;
		z-index: 50;
		width: 100%;
		height: 48px;
  }

  .van-nav-bar {
    position: fixed;
		left: 0;
		top: 0;
		width: 100%;
		height: 48px;
  }

  .van-button__text {
    color: #3497DA;
  }
</style>



